

























module.exports =
  name: 'overlayPanel'
  components:
    Window:       require './Window'
    Asteroids:    require './Asteroids'

  computed:
    activeOverlay: -> return @$store.state.activeOverlay

  methods:
    closeOverlay: ->
      @$store.commit 'SET_OVERLAY_IS_OPEN', false





