























































module.exports =
  name: 'ourServices'
  components:
    Typed: require './Typed'

  mounted: ->
    @$emit('done')

  data: ->
    showContent: false

  methods:
    openOverflowConsole: (view)->
      @$store.dispatch 'openOverflowConsole', view

  computed:
    consoleTexts: ->      return @$store.state.consoleTexts




